.slider {
  margin: auto;
  min-height: 300px;
  margin: auto;
}

.slider__item_foto {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.slider__item_text {
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;

  @include positioning-flex();

  background-color: $bgQuotation;
  padding: 30px 50px;

  @media screen and (min-width: 768px) {
    @media screen and (max-width: 1198px) {
      max-width: 620px;
      padding: 30px 50px;
    }
  }

  @media screen and (min-width: 1200px) {
    max-width: 950px;
    padding: 30px 80px;
  }
}

.slider__item_text-desc:first-child::before {
  content: '';
  position: absolute;
  top: -18px;
  left: -30px;
  justify-content: center;
  text-align: center;
  width: 54px;
  height: 40px;
  background-size: contain;
  display: inline-block;
  background-image: url(../images/quotes.svg);
  opacity: 0.7;
  z-index: -11;
}

.slider__item_text-desc {
  position: relative;
  background-image: repeating-linear-gradient(
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0) 29px,
    #e1e1e1 29px,
    #e1e1e1 30px
  );
  justify-content: center;
  text-align: center;
  z-index: 22;

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.9;
  }

  @media screen and (min-width: 768px) {
    @media screen and (max-width: 1198px) {
      font-size: 14px;
      line-height: 2.2;
    }
  }

  font-weight: 500;
  font-size: 12px;
  line-height: 2.55;
  letter-spacing: 0.04em;
}

.slider__item_author--container {
  @include positioning-flex();

  flex-direction: column;
  margin-bottom: 30px;
}

.slider__item--dot {
  content: '';
  width: 32px;
  height: 4px;
  text-align: center;
  background-size: contain;
  display: inline-block;
  background-image: url(../images/dots.svg);
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 16px;
}

.slider__item_author {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.86;
  text-align: center;
  letter-spacing: 0.04em;
}
