.button-light {
  color: #d41443;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button-light:hover, .button-light:focus {
  background-color: #ecebeb;
}

.button-dark {
  color: #fff;
  cursor: pointer;
  background-color: #d41443;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  transition: background-color .25s .25s;
  display: flex;
}

.button-dark:hover, .button-dark:focus {
  background-color: #c7133e;
}

.button-light__icon, .button-dark__icon {
  width: 5px;
  height: 8px;
